.card-catalog {
  margin: 0 auto;
  padding: 0 40px 50px;
  display: flex;
  flex-wrap: wrap;
  width: getWidthCatalog($card-width, $card-right-margin, $card-row);

  & .card:not(:nth-child(-n + 4)) {
    margin-top: 46px;
  }

  & .card:nth-child(-n + 4) {
    margin-top: 50px;
  }

  & .card:nth-child(4n + 4) {
    margin-right: 0;
  }
}
